.c-nav-anchor {
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  text-shadow: 0 0 3px #111111;
  transition: all 0.3s;
  position: relative;
  padding: 0.5rem;
  font-weight: bold;
  font-size: 1rem;
  opacity: 0.8;
}
.c-nav-anchor::before, .c-nav-anchor::after {
  content: "";
  position: absolute;
  transition: all 0.2s;
  opacity: 0;
  height: 24px;
  width: 24px;
}
.c-nav-anchor::before {
  left: 12px;
  top: 12px;
  border-top: 3px solid #a8cc32;
  border-left: 3px solid #a8cc32;
}
.c-nav-anchor::after {
  right: 12px;
  bottom: 12px;
  border-bottom: 3px solid #a8cc32;
  border-right: 3px solid #a8cc32;
}
.c-nav-anchor:hover {
  color: #a8cc32;
}
.c-nav-anchor:hover::before, .c-nav-anchor:hover::after {
  opacity: 0.8;
}
.c-nav-anchor:hover::before {
  left: 0px;
  top: 0px;
}
.c-nav-anchor:hover::after {
  right: 0px;
  bottom: 0px;
}

.calendar-frame {
