}
.cal-nav__btn:hover {
  background: rgba(255, 255, 250, 0.35);
}

.cell {
  width: 14.28%;
  padding-top: 14.28%;
  box-sizing: border-box;
  border: 1px dotted rgba(57, 66, 83, 0.23);
  position: relative;
}

.cell--not-in-month {
  background: rgba(57, 66, 83, 0.11);
}
.cell--not-in-month .cell_inner p {
  color: rgba(57, 66, 83, 0.23);
}

.cell__inner {
  background: rgba(57, 66, 83, 0.11);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  overflow: hidden;
  cursor: not-allowed;
}

.cell__day-number-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.cell__day-number {
  width: 100%;
  line-height: 100%;
  text-align: center;
}

.cell__available-data {
  border: none;
  padding-top: 4px;
  position: absolute;
  background: #fffffa;
  overflow: hidden;
  height: 100%;
  width: 100%;
  cursor: pointer;
  transition: all 0.3s;
}
.cell__available-data:before {
  content: "";
  width: 100%;
  height: 2px;
  background: #a8cc32;
  position: absolute;
  top: 0;
  left: 0;
}
.cell__available-data:hover {
  background: #a8cc32;
}

.calendar-spinner {
  position: absolute;
  height: 100%;
  width: 100%;
